/**/

//
import G from './gloable.js';

// 
import '../scss/stage.scss';

/*
*Stage  
*stageDom: canvasid
*
*/
export default function Stage(stageDom='stage'){

	const that = this;
	that.stage = new G.ctj.Stage(stageDom);
	G.stage = that.stage;
	G.ctj.Touch.enable(that.stage);

}
